.switch-toggle {
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.switch-track {
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
  background-color: #333;
  position: absolute;
  top: 0;
  left: 0;
}

.switch-marker {
  width: 13px;
  height: 13px;
  z-index: 2;
  pointer-events: none;
  background-color: #fff;
  border-radius: 50%;
  transition: transform .25s;
  position: relative;
  transform: translateX(3px);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .25);
}

.switch-toggle:checked + .switch-track {
  background-color: #333;
}

.switch-toggle:checked ~ .switch-marker {
  transform: translateX(17px);
}

.switch-toggle:focus + .switch-track, .switch-toggle:active + .switch-track {
  box-shadow: 0 0 0 3px #ff001b;
}

